@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');


.button_resume{
    display: flex;
    justify-content: center;
}

.button_r {
    margin-top: 1rem;
    background-color: #00b7ff;
    border: 0 solid #E5E7EB;
    box-sizing: border-box;
    color: #000000;
    display: flex;
    font-family: ui-sans-serif,system-ui,-apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    font-size: 1rem;
    font-weight: 700;
    justify-content: center;
    line-height: 1.75rem;
    padding: .75rem 1.65rem;
    position: relative;
    text-align: center;
    text-decoration: none #000000 solid;
    text-decoration-thickness: auto;
    width: 100%;
    max-width: 300px;
    position: relative;
    cursor: pointer;
    transform: rotate(-2deg);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.button_r:focus {
    outline: 0;
}

.button_r:after {
    content: '';
    position: absolute;
    border: 3px solid #000000;
    bottom: 4px;
    left: 4px;
    width: calc(100% - 1px);
    height: calc(100% - 1px);
}

.button_r:hover:after {
    bottom: 2px;
    left: 2px;
}

.click_this{
    display: flex;
    justify-content: center;
    font-family: 'Fredoka One', cursive;
    font-size: 20px;
}

.contact{
    display: flex;
    justify-content: center;
    font-family: 'Fredoka One', cursive;
    font-size: 20px;
}

.social_link{
    display: flex;
    justify-content: center;
    gap: 20px;
    margin: 0;
}

.facebook{
    height: 70px;
    width: 70px;
}

.line{
    height: 70px;
    width: 70px;
}

.mail{
    height: 70px;
    width: 70px;
}

.somespace{
    margin: 4rem;
}

@media (min-width: 1024px) {
    .button_r {
        background-color: #00b7ff;
        border: 0 solid #E5E7EB;
        box-sizing: border-box;
        color: #000000;
        display: flex;
        font-family: ui-sans-serif,system-ui,-apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
        font-size: 1rem;
        font-weight: 700;
        justify-content: center;
        line-height: 1.75rem;
        padding: .75rem 1.65rem;
        position: relative;
        text-align: center;
        text-decoration: none #000000 solid;
        text-decoration-thickness: auto;
        width: 100%;
        max-width: 460px;
        position: relative;
        cursor: pointer;
        transform: rotate(-2deg);
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        padding: 2rem;
        font-size: 30px;
    }
    
    .facebook{
        height: 100px;
        width: 100px;
    }
    
    .line{
        height: 100px;
        width: 100px;
    }
    
    .mail{
        height: 100px;
        width: 100px;
    }
}