@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

.font_home{
    text-align: center;
    margin-top: 1rem;
    font-size: 25px;
    color: rgb(255, 153, 0);
    font-family: 'Fredoka One', cursive;
}

.font_home span{
    color: rgb(255, 255, 255);
}

.image_home{
    margin-top: 10px;
    height: auto;
    max-width: 300px;
    width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.responsive_home{
    margin-top: 2rem;
}

@media(min-width: 768px) {
    .image_home{
        height: auto;
        max-width: 100%;
        width: 70%;
    }

    .responsive_home{
        margin-top: 5rem;
        font-size: 25px;
        font-family: 'Fredoka One', cursive;
    }

    .font_home{
            text-align: center;
            margin-top: 8rem;
            font-size: 50px;
            color: rgb(255, 153, 0);
            font-family: 'Fredoka One', cursive;
    }

    .font_home span{
        color: rgb(255, 255, 255);
    }
    
}



