@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

body{
    color: #fff;
}

.about_me{
    font-size: 12px;
    font-family: 'Fredoka One', cursive;
}

.container_about {
    background-color: rgb(0, 0, 34);
    justify-content: center;
    display: flex;
    gap: 1rem;
    align-items: center;
    font-size: 8px;
    margin-top: 2rem;
}

.profile_image {
    -webkit-transition: 1s;
	-moz-transition: 1s;
	transition: 1s;
	-webkit-border-radius: 20px;
	-moz-border-radius: 20px;
	border-radius: 20px;
    border-radius: 20px;
    width: auto;
    max-width: 30%;
    height: auto;
}

.title{
    color: rgb(0, 43, 185);
}

@media(min-width: 1200px) {
    .container_about {
        background-color: rgb(0, 0, 34);
        justify-content: center;
        display: flex;
        gap: 5rem;
        align-items: center;
        font-size: 20px;
        margin-top: 5rem;
    }

    .about_me{
        font-size: 35px;
        font-family: 'Fredoka One', cursive;
    }
    
}