@import 'https://fonts.googleapis.com/css?family=Raleway';
@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');

.header{
    position: sticky;
    top: 0;
    width: 100%;
}

.logo{
    font-family: 'Fredoka One', cursive;
    color: rgb(0, 204, 255);
    display: inline-block;
    font-size: 20px;
    margin-left: 30px;
    margin-top: 25px;
}

.logo span{
    color: #FFF;
}

.nav{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.menu a{
    display: block;
    padding: 30px;
    color: #f9f9f9;
}

.menu a:hover{
    background-color: #85888C;
}

.nav{
    max-height: 0;
    transition: max-height .5s ease-out;
}

.hamb{
    cursor: pointer;
    float: right;
    padding: 40px 20px;
}

.hamb-line {
    background: #f9f9f9;
    display: block;
    height: 2px;
    position: relative;
    width: 24px;

}

.hamb-line::before,
.hamb-line::after{
    background: #f9f9f9;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
}
.hamb-line::before{
    top: 5px;
}
.hamb-line::after{
    top: -5px;
}

.side-menu {
    display: none;
}

.side-menu:checked ~ nav{
    max-height: 100%;
}
.side-menu:checked ~ .hamb .hamb-line {
    background: transparent;
}
.side-menu:checked ~ .hamb .hamb-line::before {
    transform: rotate(-45deg);
    top:0;
}
.side-menu:checked ~ .hamb .hamb-line::after {
    transform: rotate(45deg);
    top:0;
}

@media (min-width: 1024px) {
    .logo{
        margin-top: 60px;
        font-family: 'Fredoka One', cursive;
        color: rgb(0, 204, 255);
        display: inline-block;
        font-size: 50px;
        margin-left: 30px;
    }
    .nav{
        max-height: none;
        top: 0;
        position: relative;
        float: right;
        width: fit-content;
    }
    .menu li{
        float: left;
    }
    .menu a:hover{
        background-color: transparent;
        color: #85888C;
    }

    .hamb{
        display: none;
    }

    div.container a {
        font-family: Raleway;
        font-size: 20px;    
        position: relative;
        z-index: 0;
        cursor: pointer;
    }

    div.naveffect a:before, div.naveffect a:after {
        position: absolute;
        opacity: 0;
        width: 0%;
        height: 2px;
        content: '';
        background: #FFF;
        transition: all 0.3s;
    }
    
    div.naveffect a:before {
        left: 0px;
        top: 0px;
    }
    
    div.naveffect a:after {
        right: 0px;
        bottom: 0px;
    }
    
    div.naveffect a:hover:before, div.naveffect a:hover:after {
        opacity: 1;
        width: 100%;
    }
}